
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
XHeader,
InlineLoading
},
beforeRouteLeave(to, from, next) { // 用于做缓存

if (to.name == 'drugsDetails' || to.name == 'drugsSearch') {

localStorage.setItem('lastcodeduge_Y', this.$route.params.code || window.localStorage.lastcodeduge_Y)
localStorage.setItem('lastcodeduge_N', this.$route.params.name || window.localStorage.lastcodeduge_N)
} else {
localStorage.removeItem('lastcodeduge_Y');
localStorage.removeItem('lastcodeduge_N');
}
next();
},
data() {
return {
pageIndex: 1,
loconame: '',
pageSize: 15,
list: [














]
};
},
mounted() {
this.loconame = this.$route.params.name || window.localStorage.lastcodeduge_N;
this.initdata();
},
methods: {

initdata() {
this.$ajax.post(this.$root.host, {
'MSH.1': 'drugpool',
'MSH.2': 'listDrug',
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'typeBm': this.$route.params.code || window.localStorage.lastcodeduge_Y,
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(data)
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
if (typeof data != 'undefined') { //判断是否空对象
if (typeof data.rows != 'undefined' && data.rows.length > 0) {

if (this.pageIndex == 1) {
this.list = data.rows
} else {
this.list = this.list.concat(data.rows);
}
} else {
this.judge()
}
} else {
this.judge()
}
})
},
judge() { //无数据判断
if (this.pageIndex > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.list = [];
}
},
pullingDown() {
this.pageIndex = 1;
this.initdata();
},
pullingUp() {
this.pageIndex++;
this.initdata();
},
todrugDetail(code) {
this.$router.push({
name: 'drugsDetails',
params: {
code: code,
}
})
},
searchbt() {
this.$router.push({
name: "drugsSearch",
});
}
},
};



